
.slide-fade-enter-active {
  -webkit-transition: all .3s ease;
  transition: all .3s ease;
}
.slide-fade-leave-active {
  -webkit-transition: all 0.8s cubic-bezier(1, 0.5, 0.8, 1);
  transition: all 0.8s cubic-bezier(1, 0.5, 0.8, 1);
}
.slide-fade-enter, .slide-fade-leave-to {
  -webkit-transform: translateY(-10px);
          transform: translateY(-10px);
  opacity: 0;
}
.Sidebar {
  padding: 1em 2em;
  width: 20%;
  min-width: 20%;
}
.Sidebar h3 {
    font-weight: 500;
}
.FoodTypes .FoodTypes__list .FoodTypes__item {
  padding: 0 1em;
  border-radius: 5px;
  line-height: 1.7em;
  cursor: pointer;
}
.FoodTypes .FoodTypes__list .FoodTypes__item:hover, .FoodTypes .FoodTypes__list .FoodTypes__item:active, .FoodTypes .FoodTypes__list .FoodTypes__item.FoodTypes__selected {
    background: #3098c3;
    color: #fff;
}
.Ratings .Ratings__ranges {
  margin-left: 1em;
}
.Ratings .Ratings__ranges .Ratings__selected {
    -webkit-filter: contrast(200%);
            filter: contrast(200%);
}
.Ratings .Ratings__stars {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  line-height: 1.7em;
  cursor: pointer;
}
.Ratings .Ratings__stars:hover {
    -webkit-filter: contrast(200%);
            filter: contrast(200%);
}
.PaymentOptions .PaymentOptions__list .PaymentOptions__item {
  padding: 0 1em;
  border-radius: 5px;
  line-height: 1.7em;
  cursor: pointer;
}
.PaymentOptions .PaymentOptions__list .PaymentOptions__item:hover, .PaymentOptions .PaymentOptions__list .PaymentOptions__item:active, .PaymentOptions .PaymentOptions__list .PaymentOptions__item.PaymentOptions__selected {
    background: #3098c3;
    color: #fff;
}



